.win-popup {
  display: flex;
  flex-direction: column;
  justify-content: center;

  & .modal__content {
    min-width: 35rem;
    min-height: 20rem;
    box-shadow: 0 0 10rem 1rem $color-font-light-gray;
  }

  &_title {
    width: 100%;
    padding: 1rem 0 1.4rem;
    border-bottom: 0.2rem solid;
    font-size: 2.6rem;
    text-transform: uppercase;
    text-align: center;
    color: $color-font-win;
  }

  &_info {
    display: grid;
    place-items: center;
    font-size: 2rem;
    margin: 3rem 0;
    color: $color-font-info;
  }
}
